@import '../../services/styleHelpers/colors';
@import '../../services/styleHelpers/animations.scss';

.registerButton {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: $color-dark-blue;
  text-align: center;
  color: $color-white;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  display: block;
  z-index: 1000;
  line-height: 45px;
  -webkit-box-shadow: 0 0 10px 5px rgba(0,0,0,0.15);
  box-shadow: 0 0 10px 5px rgba(0,0,0,0.15);

  @include transition;

  &:hover {
    background: $color-dark-red;
    color: $color-white;
  }
}
