@import './animations';
@import './colors';

html, body {
  line-height: 140%;
}

a {
  color: $color-dark-blue;
  font-weight: 500;
  @include transition;

  &:hover {
    color: $color-dark-red;
  }
}

h1 {
  font-size: 22px;
  color: $color-dark-blue;
  text-transform: uppercase;
  font-weight: 700;
  padding: 0 0 12px;
  font-family: $font-family-ultra;
}

h2 {
  font-size: 17px;
  color: $color-dark-red;
  text-transform: uppercase;
  font-weight: 700;
  padding: 0 0 7px;

}

h3 {
  font-size: 20px;
  font-weight: 700;
}

p {
  margin: 0 0 10px;
}
